import React, { Component } from "react";
import DataBox from "../../common/DataBox";
import DataBox2 from "../../common/DataBox2";
// import MRbox from "../../common/MRbox";
import KSHbox from "../../common/KSHbox";
import {
  emptyChartConfig,
  multiBarConfig,
  pieConfig,
  stackBarConfig,
  heatMapConfig,
  columnRangeConfig,
  pyramidConfig,
  syncChartConfig
} from "../../../config/chartconfig";
import { getFactoryGPU, getFactoryTestData } from "../../../services/Service";
import cloneDeep from 'lodash/cloneDeep';

// let boxHeight = 209;

export class Phase2 extends Component {
  constructor(props) {
    super(props);

    this.nameMap = {
      "1": "实际能耗",
      "2": "理论能耗",
      "4": "实际单耗",
      "5": "理论单耗",
      "9": "实际产量",
      "10": "理论产量"
    };

    let ecc = Object.assign({}, emptyChartConfig, {
      chart: { height: 220, backgroundColor: "transparent" }
    });
    let ecc2 = Object.assign({}, emptyChartConfig, {
      chart: { height: 256, backgroundColor: "transparent" }
    });

    this.state = {
      httpLoading: false,
      loading: true,
      chart1: ecc,
      chart2: ecc,
      chart3: ecc,
      chart4: ecc2,
      chart5: ecc2,
      chart6: ecc2,
      chart7: ecc,
      chart8: ecc,
      chart9: ecc,
      chart10: ecc,
      chart11: ecc
    };
  }

  componentDidMount = () => {
    let query = {
      start: "2018-11-15",
      end: "2018-11-16"
    };
    if (false) {
      this.setState({ httpLoading: true });
      getFactoryGPU(query).then(result => {
        console.log(result);
        this.setState((state, props) => {
          return {
            loading: false,
            cpuChart: this.getMultiBarConfig(result["bar_data"], [4, 5])
          };
        });
        this.setState({ httpLoading: false });
      });
    } else {
      this.setState({ httpLoading: true });
      let result = getFactoryTestData();
      let chart1 = this.getStackBarConfig(result["stack_data"]);
      let chart2 = this.getHeatMapConfig();
      let chart3 = this.getMultiBarConfig(result["bar_data"], [4, 5]);
      
      let chart7 = this.getPieConfig(result["pie_data"]);
      let chart8 = this.getPyramidConfig();
      let chart9 = this.getPieConfig(result["pie_data"]);
      let chart10 = this.getColumnRangeConfig();
      let chart11 = this.getPieConfig(result["pie_data"]);

      let chart4 = this.getSyncChartConfig(result["sync_data"], 0);
      let chart5 = this.getSyncChartConfig(result["sync_data"], 1);
      let chart6 = this.getSyncChartConfig(result["sync_data"], 2);
      setTimeout(() => {
        this.setState((state, props) => {
          return {
            loading: false,
            chart1,
            chart2,
            chart3,
            chart4,
            chart5,
            chart6,
            chart7,
            chart8,
            chart9,
            chart10,
            chart11
          };
        });
        this.setState({ httpLoading: false });
      }, 500);
    }
  };

  genSeries = (data, key, rp, tp) => {
    let rpd = {},
      tpd = {};
    let re = {
      name: this.nameMap[key[0]],
      data: data.map(value => value[key[0]])
    };
    let te = {
      name: this.nameMap[key[1]],
      data: data.map(value => value[key[1]])
    };
    if (rp && tp) {
      rpd = {
        name: this.nameMap[9],
        data: data.map(v => v[0]).fill(rp),
        type: "line",
        yAxis: 1
      };
      tpd = {
        name: this.nameMap[10],
        data: data.map(v => v[0]).fill(tp),
        type: "line",
        yAxis: 1
      };
      return [re, te, rpd, tpd];
    } else {
      return [re, te];
    }
  };

  getMultiBarConfig = (item, key, chartHeight = 220) => {
    // let newchart = Object.assign({}, multiBarConfig);
    let newchart = JSON.parse(JSON.stringify(multiBarConfig));
    newchart.chart.height = chartHeight;
    newchart.chart.type = "spline";
    if (chartHeight > 220) {
      newchart.chart.type = "column";
    }
    newchart.series = this.genSeries(item, key);
    newchart.xAxis.categories = item.map(v => v[0]);
    return newchart;
  };

  getPieConfig = item => {
    let newchart = JSON.parse(JSON.stringify(pieConfig));
    let metadata = item.slice();
    newchart.series[0].data = metadata;
    return newchart;
  };

  getStackBarConfig = (item, empty) => {
    let newchart = JSON.parse(JSON.stringify(stackBarConfig));
    newchart.series = item.metadata.slice();
    if (item.tariff && item.tariff.data.length > 0) {
      let tariff = Object.assign({}, item.tariff, {
        type: "line",
        step: "left",
        yAxis: 1,
        color: "purple"
      });
      newchart.series.push(tariff);
    }
    return newchart;
  };

  getHeatMapConfig = () => {
    let newchart = JSON.parse(JSON.stringify(heatMapConfig));
    newchart = heatMapConfig;
    return newchart;
  };

  getColumnRangeConfig = () => {
    let newchart = JSON.parse(JSON.stringify(columnRangeConfig));
    return newchart;
  };

  getPyramidConfig = () => {
    let newchart = JSON.parse(JSON.stringify(pyramidConfig));
    return newchart;
  };

  getSyncChartConfig = (data, key) => {
    let colors = ['#67b7dc','#6794dc','#6771dc'];
    let newchart = cloneDeep(syncChartConfig);
    newchart.title.text = data.metadata[key].name;
    newchart.series[0].data = data.metadata[key].data;
    newchart.series[0].name = data.metadata[key].name;
    newchart.series[0].type = data.metadata[key].type;
    newchart.series[0].color = colors[key];
    newchart.series[0].tooltip = {valueSuffix: ' '+ data.metadata[key].unit}
    return newchart;
  };

  render() {
    return (
      <div className="row">
        <div className="panel">
          <div className="panel-body">
            <div className="col-md-2">
              <div className="row pl10 pr10 group-kshbox">
                <div className="col-md-6">
                  <KSHbox />
                </div>
                <div className="col-md-6">
                  <KSHbox />
                </div>
                <div className="col-md-6">
                  <KSHbox />
                </div>
                <div className="col-md-6">
                  <KSHbox />
                </div>
              </div>

              <DataBox chart={this.state.chart1} loading={this.state.loading} />
              <DataBox chart={this.state.chart10} loading={this.state.loading} />
            </div>
            <div className="col-md-2">
              <DataBox chart={this.state.chart7} loading={this.state.loading} />
              <DataBox chart={this.state.chart8} loading={this.state.loading} />
              <DataBox chart={this.state.chart9} loading={this.state.loading} />
            </div>
            <div className="col-md-3">
              <DataBox chart={this.state.chart1} loading={this.state.loading} />
              <DataBox chart={this.state.chart3} loading={this.state.loading} />
              <DataBox chart={this.state.chart2} loading={this.state.loading} />
            </div>
            <div className="col-md-5">
              <DataBox2
                chart={this.state.chart4} 
                chart2={this.state.chart5} 
                chart3={this.state.chart6} 
                loading={this.state.loading} />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Phase2;
